<template>
  <v-app-bar class="header-bar">
    <div class="header-logo-container">
      <img class="header-logo" :src="logo" alt="FeelFinder Logo" />
    </div>
    <v-spacer></v-spacer>
    <div
      @click="goTo(item.path)"
      class="header-options"
      v-for="item in opciones"
      :key="item"
    >
      <p>{{ $t(item.nameKey).toUpperCase() }}</p>
    </div>
  </v-app-bar>
</template>
<script>
export default {
  data() {
    return {
      logo: require("../assets/FeelFinder_logo_white.png"),
      opciones: [
        {
          nameKey: "home.main", // Clave para la traducción
          path: "/",
        },
        {
          nameKey: "home.analyze",
          path: "/analyze",
        },
        {
          nameKey: "home.audio",
          path: "/audio",
        },
        {
          nameKey: "home.images",
          path: "/image",
        },
        {
          nameKey: "home.about",
          path: "/about",
        },
        {
          nameKey: "home.contact",
          path: "/contact",
        },
      ],
    };
  },
  methods: {
    goTo(path) {
      this.$router.push(path);
    },
  },
};
</script>
<style></style>
