<template>
  <ErrorAlert
    v-if="showAlert"
    :isVisible="showAlert"
    @close-alert="closeAlert"
    :text="errorText"
  />
  <div class="main-container2">
    <div class="search-container">
      <div class="main-input-image">
        <!-- Entrada de archivo -->
        <p class="p-image">{{ $t("text.generate") }}</p>
        <v-select
          v-if="translatedFeelings"
          class="select-image"
          density="compact"
          width="200"
          :items="translatedFeelings"
          variant="solo-filled"
          item-value="value"
          item-title="label"
          v-model="selectedFeeling"
        ></v-select>

        <v-btn
          @click="sendData"
          prepend-icon="mdi-send"
          class="button margin-left"
          :disabled="!isFeelingSelected"
        >
          {{ $t("buttons.generate") }}
        </v-btn>
      </div>
    </div>
    <div v-if="isSelected" class="generation-container">
      <div class="generations">
        <div class="generated-column">
          <div
            class="generated-image-container"
            :class="{ 'card-skeleton': isLoading }"
          >
            <div v-if="isLoading" class="loader"></div>
            <img v-else class="image" :src="image1" />
          </div>
          <div v-if="isLoading" class="timer-container">
            <v-icon class="time-icon">mdi mdi-clock-outline</v-icon>
            <p>{{ formattedTime }}</p>
          </div>
        </div>
        <div class="generated-column">
          <div
            class="generated-image-container"
            :class="{ 'card-skeleton': isLoading }"
          >
            <div v-if="isLoading" class="loader"></div>
            <img v-else class="image" :src="image2" />
          </div>
          <div v-if="isLoading" class="timer-container">
            <v-icon class="time-icon">mdi mdi-clock-outline</v-icon>
            <p>{{ formattedTime }}</p>
          </div>
        </div>
      </div>
      <div class="analyze-card">
        <div v-if="isLoading" class="text-loader-container">
          <div class="text-loader">
            <p>{{ $t("images.loader_title") }}</p>
            <div class="words">
              <span class="word">{{ $t("images.loader_word1") }}</span>
              <span class="word">{{ $t("images.loader_word2") }}</span>
              <span class="word">{{ $t("images.loader_word3") }}</span>
              <span class="word">{{ $t("images.loader_word4") }}</span>
              <span class="word">{{ $t("images.loader_word1") }}</span>
            </div>
          </div>
        </div>
        <div v-else class="text-container">
          <h2 class="detail-title font-size-24px">
            {{ $t("images.desc_title1") }}
          </h2>
          <p class="detail-desc">{{ description["Emotions and feelings"] }}</p>
          <h2 class="detail-title font-size-24px">
            {{ $t("images.desc_title2") }}
          </h2>
          <p class="detail-desc">
            {{ description["Artistic interpretation"] }}
          </p>
          <h2 class="detail-title font-size-24px">
            {{ $t("images.desc_title3") }}
          </h2>
          <p class="detail-desc">{{ description["Concise description"] }}</p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import ErrorAlert from "@/components/ErrorAlert.vue";
import { processText } from "@/services/api";
export default {
  components: {
    ErrorAlert,
  },
  data() {
    return {
      feelings: [
        "happiness",
        "sadness",
        "anger",
        "fear",
        "love",
        "joy",
        "guilt",
        "shame",
        "hope",
        "pride",
        "envy",
        "gratitude",
        "loneliness",
        "excitement",
        "boredom",
        "disgust",
        "compassion",
        "relief",
        "curiosity",
        "anxiety",
        "contentment",
        "frustration",
        "nostalgia",
        "confusion",
        "inspiration",
      ],
      selectedFeeling: null,
      isLoading: true,
      elapsedTime: 0,
      timer: null,
      image1: "",
      image2: "",
      isSelected: false,
      API_URL: "http://localhost:3000/api",
      description: "",
    };
  },
  computed: {
    isFeelingSelected() {
      return this.selectedFeeling != null;
    },
    translatedFeelings() {
      return this.feelings.map((feeling) => ({
        value: feeling,
        label: this.$t(`feelings.${feeling}`),
      }));
    },
    formattedTime() {
      const seconds = Math.floor(this.elapsedTime / 1000);
      const milliseconds = Math.floor(this.elapsedTime % 1000);
      return `${seconds}.${milliseconds.toString().padStart(3, "0")} s`;
    },
  },
  methods: {
    startTimer() {
      this.elapsedTime = 0; // Reinicia el tiempo
      this.timer = setInterval(() => {
        this.elapsedTime += 10; // Incrementa cada 10 ms
      }, 10);
    },
    stopTimer() {
      clearInterval(this.timer); // Detiene el temporizador
      this.timer = null;
    },
    async sendData() {
      this.isSelected = true;
      this.isLoading = true;
      this.startTimer();

      let textSelected = "Generate the feeling of " + this.selectedFeeling;
      console.log("Sentimiento", this.selectedFeeling);

      const maxRetries = 3;

      const wait = (ms) => new Promise((resolve) => setTimeout(resolve, ms));

      const attemptRequest = async (action, ...args) => {
        let attempt = 0;
        while (attempt < maxRetries) {
          try {
            const response = await action(...args);

            return response;
          } catch (error) {
            attempt++;
            console.error(
              `Error en el intento ${attempt} para ${action.name}:`,
              error.message
            );

            if (attempt < maxRetries) {
              console.log(
                `Esperando 1 segundo antes del intento ${attempt + 1}`
              );
              await wait(1000);
            } else {
              this.showAlert = true;
              this.isLoading = false;
              throw new Error(
                `Error persistente en ${action.name}: ${error.message}`
              );
            }
          }
        }
      };

      const imageResponse = await attemptRequest(
        processText,
        "generate-image",
        textSelected
      );
      this.stopTimer();
      this.isLoading = false;
      this.image1 = `${this.API_URL.replace("/api", "")}${
        imageResponse.imageUrls[0]
      }`;
      this.image2 = `${this.API_URL.replace("/api", "")}${
        imageResponse.imageUrls[1]
      }`;
      const responseDescription = imageResponse.description;

      // Separar los apartados en un objeto
      const parseDescription = (description) => {
        const sections = description.split("\n\n").reduce((acc, section) => {
          const [key, ...value] = section.split(": ");
          if (key && value.length > 0) {
            acc[key.trim()] = value.join(": ").trim();
          }
          return acc;
        }, {});
        return sections;
      };

      this.description = parseDescription(responseDescription);
    },
  },
};
</script>
<style scoped></style>
