<template>
  <div
    ref="alert"
    :class="[isActive ? 'alert-container' : 'alert-container-leave']"
  >
    <v-alert
      density="compact"
      :text="text"
      title="Error"
      type="error"
      width="600"
      color="#EF665B"
    >
      <v-progress-linear
        class="progress"
        color="#ebb2ad"
        v-model="percentage"
      ></v-progress-linear>
    </v-alert>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isActive: false,
      percentage: 100,
      progressInterval: 0,
    };
  },
  props: {
    text: {
      type: String,
      required: true,
    },
    isVisible: {
      type: Boolean,
      required: true,
    },
  },
  created() {
    this.isActive = this.isVisible;
  },
  mounted() {
    this.startBuffer();
    setTimeout(() => {
      this.isActive = false;
      setTimeout(() => {
        {
          this.$emit("close-alert");
        }
      }, 900);
    }, 5000);
  },
  beforeUnmount() {
    clearInterval(this.interval);
  },
  methods: {
    startBuffer() {
      clearInterval(this.interval);
      this.interval = setInterval(() => {
        this.percentage -= 20;
      }, 1000);
    },
  },
};
</script>

<style scoped>
.alert-container {
  position: absolute;
  bottom: 3rem;
  right: 1rem;
  z-index: 9999;
  animation: pop-up 1s ease-out;
}

.alert-container-leave {
  position: absolute;
  bottom: 3rem;
  right: 1rem;
  z-index: 9999;
  animation: pop-down 1s ease-in;
}

@keyframes pop-up {
  0% {
    bottom: -12rem;
  }
  100% {
    bottom: 3rem;
  }
}

@keyframes pop-down {
  0% {
    bottom: 3rem;
  }
  100% {
    bottom: -12rem;
  }
}

.progress {
  margin-top: 10px;
  margin-bottom: 10px;
}
</style>
