<template>
  <v-card
    :class="{
      'card-background-good': label == 'POSITIVE',
      'card-background-neutral': label == 'NEUTRAL',
      'card-background-bad': label == 'NEGATIVE',
    }"
  >
    <v-card-title class="align-horizontal-left card-secondary-background">
      <v-icon class="card-icon-good" v-if="label == 'POSITIVE'"
        >mdi-emoticon-happy-outline</v-icon
      >
      <v-icon class="card-icon-neutral" v-else-if="label == 'NEUTRAL'"
        >mdi-emoticon-neutral-outline</v-icon
      >
      <v-icon class="card-icon-bad" v-else>mdi-emoticon-angry-outline</v-icon>
      <div>
        <h2 class="card-title">{{ formatDesc(title, 20) }}</h2>
        <h3 class="card-subtitle">{{ tag }}</h3>
      </div>
    </v-card-title>
    <v-card-text class="card-secondary-background">
      <p class="card-description">{{ formatDesc(description, 50) }}</p>
      <p class="card-percentage">
        {{ $t("buttons.percentage") }}: {{ parseFloat(percentage).toFixed(2) }}%
      </p>
    </v-card-text>
  </v-card>
</template>
<script>
export default {
  props: {
    title: {
      type: String,
      default: "",
    },
    tag: {
      type: String,
      default: "",
    },
    description: {
      type: String,
      default: "",
    },
    percentage: {
      type: Number,
      default: 0,
    },
    label: {
      type: String,
      default: "",
    },
  },
  methods: {
    formatDesc(desc, lim) {
      // Si la longitud del texto es menor o igual al límite, devuelve el texto original
      if (desc.length <= lim) {
        return desc;
      }
      // Corta el texto al límite de caracteres y añade "..."
      return desc.substring(0, lim) + "...";
    },
  },
};
</script>
<style></style>
