<template>
  <LoaderComponent :isLoading="loading" :progress="progress" />
  <div class="main-container2">
    <div class="search-container">
      <div class="main-input2">
        <input
          class="input2"
          v-model="searchText"
          :placeholder="$t('text.theme')"
        />
        <v-btn
          @click="searchData(searchText)"
          prepend-icon="mdi-magnify"
          class="button"
          :disabled="!searchText.trim()"
        >
          {{ $t("buttons.search") }}
        </v-btn>
      </div>
      <div class="button-container">
        <v-btn v-for="button in buttons" :key="button" class="button2">
          <v-icon class="button-icon">{{ button }}</v-icon>
        </v-btn>
      </div>
    </div>

    <div class="cards-and-filters">
      <div class="cards-column">
        <v-row dense class="cards-grid">
          <v-col
            v-for="card in displayedCards"
            :key="card.id"
            cols="12"
            md="6"
            lg="4"
            xl="3"
          >
            <SentimentCardComponent
              @click="goToDetails(card)"
              :title="card.title"
              :tag="card.tag"
              :description="card.description"
              :percentage="card.percentage"
              :label="card.sentiment"
            />
          </v-col>
        </v-row>
      </div>

      <div class="filters-column">
        <v-btn
          v-for="filter in filters"
          :key="filter"
          @click="toggleFilter(filter)"
          :class="{ 'active-filter': activeFilters[filter] }"
          class="filter-button"
        >
          {{ formatFilters(filter, $t) }}
        </v-btn>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted, watch } from "vue";
import { useRouter, useRoute } from "vue-router";
import { useStore } from "vuex";
import { getComments } from "@/services/api";
import LoaderComponent from "@/components/LoaderComponent.vue";
import SentimentCardComponent from "@/components/SentimentCardComponent.vue";

export default {
  name: "SentimentDashboard",
  components: {
    SentimentCardComponent,
    LoaderComponent,
  },
  setup() {
    const store = useStore();
    const router = useRouter();
    const route = useRoute();

    const loading = ref(false);
    const allCards = ref([]);
    const progress = ref(0);
    const filters = ["POSITIVE", "NEUTRAL", "NEGATIVE"];
    const buttons = ref(["mdi mdi-reddit"]);
    const searchText = ref(route.query.search || "");
    const activeFilters = ref({
      POSITIVE: false,
      NEUTRAL: false,
      NEGATIVE: false,
    });

    const initializeData = () => {
      const searchCards = store.getters.searchCards;

      if (searchCards.length > 0) {
        allCards.value = searchCards.map((card, index) => ({
          ...card,
          id: `card-${index}`,
        }));
      }
    };
    // Realizar la búsqueda cuando se carga la vista o cambia el término de búsqueda
    watch(
      () => route.query.search,
      (newSearch) => {
        searchText.value = newSearch || ""; // Actualizar el searchText con la query de la URL
        searchData(searchText.value); // Ejecutar búsqueda al cambiar la query
      }
    );

    const searchData = async (text) => {
      progress.value = 0;
      loading.value = true;
      allCards.value = [];
      let progressInterval = setInterval(() => {
        if (progress.value < 100) {
          progress.value += 1;
        }
      }, 350);

      try {
        const response = await getComments(text);
        clearInterval(progressInterval);
        allCards.value = response.comments.map((card, index) => ({
          ...card,
          id: `card-${index}`,
        }));
        store.commit("setsearchCards", response.comments);
        progress.value = 100;
      } catch (error) {
        console.error("Error loading cards:", error);
        clearInterval(progressInterval);
      } finally {
        loading.value = false;
      }
    };

    const goToDetails = (card) => {
      console.log("CARD: ", card);

      store.commit("setSelectedCard", card);
      router.push({
        name: "AnalyzeDetail",
        params: { name: card.title },
      });
    };

    const toggleFilter = (filter) => {
      activeFilters.value[filter] = !activeFilters.value[filter];
    };

    const formatFilters = (filter, t) => {
      const filterMap = {
        POSITIVE: "buttons.positive", // Clave de traducción
        NEUTRAL: "buttons.neutral",
        NEGATIVE: "buttons.negative",
      };
      const translationKey = filterMap[filter] || filter;
      return t(translationKey); // Usamos el método de traducción
    };

    const displayedCards = computed(() => {
      const activeFiltersList = Object.entries(activeFilters.value)
        .filter(([_, isActive]) => isActive)
        .map(([filter, _]) => filter);

      if (activeFiltersList.length === 0) {
        return allCards.value;
      }

      return allCards.value.filter((card) =>
        activeFiltersList.includes(card.sentiment)
      );
    });

    onMounted(() => {
      initializeData();
      if (route.query.search) {
        searchData(route.query.search);
      }
    });

    return {
      loading,
      progress,
      filters,
      buttons,
      searchText,
      activeFilters,
      displayedCards,
      searchData,
      goToDetails,
      toggleFilter,
      formatFilters,
    };
  },
};
</script>

<style scoped></style>
