import { createStore } from "vuex";
import { setLocale } from "@/i18n";
import { getTopComments } from "@/services/api";

export default createStore({
  state: {
    cards: JSON.parse(localStorage.getItem("cards")) || [],
    lastUpdated: localStorage.getItem("lastUpdated") || 0,
    selectedCard: JSON.parse(localStorage.getItem("selectedCard")) || null, // Leer del localStorage si existe
    searchCards: [],
    language: localStorage.getItem("selectedLanguage") || "es",
  },
  mutations: {
    setLanguage(state, language) {
      state.language = language;
    },
    setCards(state, cards) {
      state.cards = cards;
      localStorage.setItem("cards", JSON.stringify(cards));
    },
    setLastUpdated(state, timestamp) {
      state.lastUpdated = timestamp;
      localStorage.setItem("lastUpdated", timestamp);
    },
    setSelectedCard(state, card) {
      state.selectedCard = card;
      localStorage.setItem("selectedCard", JSON.stringify(card)); // Guardar en localStorage
    },
    setsearchCards(state, card) {
      state.searchCards = card;
    },
  },
  actions: {
    async changeLanguage({ commit }, language) {
      commit("setLanguage", language);
      await setLocale(language); // Cambiar el idioma en Vue I18n
    },
    async fetchCards({ commit, state }) {
      const currentTimestamp = Date.now();
      const timeDiff = currentTimestamp - state.lastUpdated;

      if (timeDiff > 20 * 60 * 1000 || state.cards.length === 0) {
        try {
          const response = await getTopComments();
          if (response) {
            commit("setCards", response);
            commit("setLastUpdated", currentTimestamp);
          }
        } catch (error) {
          console.log("Error al obtener las cards:", error);
        }
      }
    },
    selectCard({ commit }, card) {
      commit("setSelectedCard", card);
    },
    setsearchCards({ commit }, card) {
      commit("setsearchCards", card);
    },
  },
  getters: {
    cards: (state) => state.cards,
    selectedCard: (state) =>
      state.selectedCard || JSON.parse(localStorage.getItem("selectedCard")), // Leer del localStorage si no está en el estado
    searchCards: (state) => state.searchCards,
    language: (state) => state.language,
  },
});
