<template>
  <div class="wrapper">
    <input
      type="checkbox"
      id="toogle"
      ref="toggleInput"
      class="hidden-trigger"
    />
    <label for="toogle" class="circle">
      <img :src="imageSelected" alt="Selected icon" />
    </label>

    <div class="subs">
      <button
        v-for="(icon, index) in icons"
        :key="index"
        class="sub-circle"
        @click="selectLanguage(icon.language)"
      >
        <img :src="icon.image" alt="Icon option" />
      </button>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";

export default {
  data() {
    return {
      icons: [
        { language: "es", image: require("../assets/spain_icon.png") },
        { language: "fr", image: require("../assets/france_icon.png") },
        { language: "it", image: require("../assets/italy_icon.png") },
        { language: "de", image: require("../assets/germany_icon.png") },
        { language: "en", image: require("../assets/united-kingdom_icon.png") },
      ],
    };
  },
  computed: {
    ...mapState(["language"]),
    imageSelected() {
      const selectedIcon = this.icons.find(
        (icon) => icon.language === this.language
      );
      return selectedIcon ? selectedIcon.image : this.icons[0].image;
    },
  },
  methods: {
    ...mapActions(["changeLanguage"]),
    async selectLanguage(language) {
      await this.changeLanguage(language);
    },
  },
};
</script>

<style scoped></style>
