import { createI18n } from "vue-i18n";

// Función para cargar dinámicamente los mensajes según el idioma
async function loadLocaleMessages(locale) {
  try {
    const messages = await import(`./locales/${locale}.json`);
    return messages.default || messages;
  } catch (error) {
    console.error(`Error loading messages for locale: ${locale}`, error);
    return {};
  }
}

// Idioma predeterminado
const defaultLocale = localStorage.getItem("selectedLanguage") || "es";

// Configuración inicial
export const i18n = createI18n({
  legacy: false,
  locale: defaultLocale,
  fallbackLocale: "es",
  messages: {}, // Inicialmente vacío, se llenará dinámicamente
});

// Método para cambiar idioma dinámicamente
export async function setLocale(locale) {
  const messages = await loadLocaleMessages(locale);
  i18n.global.setLocaleMessage(locale, messages);
  i18n.global.locale.value = locale;
  localStorage.setItem("selectedLanguage", locale);
}

// Cargar mensajes para el idioma predeterminado al inicio
setLocale(defaultLocale);

export default i18n;
