<template>
  <LoaderComponent :isLoading="loading" :progress="progress" />
  <ErrorAlert
    v-if="showAlert"
    :isVisible="showAlert"
    @close-alert="closeAlert"
    :text="errorText"
  />
  <div class="main-container2">
    <div class="search-container">
      <div class="main-input2">
        <!-- Entrada de archivo -->
        <input
          ref="fileInput"
          class="input-file"
          type="file"
          @change="handleFileChange"
          accept="audio/*"
        />
        <v-btn
          @click="toggleRecording"
          :prepend-icon="isRecording ? 'mdi-stop-circle' : 'mdi-microphone'"
          class="button"
        >
          {{ isRecording ? $t("buttons.stop") : $t("buttons.record") }}
        </v-btn>
        <v-btn
          @click="sendData"
          prepend-icon="mdi-send"
          class="button margin-left"
          :disabled="!isAFile"
        >
          {{ $t("buttons.send") }}
        </v-btn>
      </div>
    </div>
    <div v-if="dataIsLoaded && !loading">
      <v-card class="card-audio">
        <v-card-title>
          <h2 v-if="this.selectedFile != null">{{ this.selectedFile.name }}</h2>
        </v-card-title>
        <v-card-text class="card-audio-body">
          <v-row>
            <v-col cols="4">
              <div class="card-audio-img-container">
                <img v-if="imageIsLoaded" class="card-audio-img" :src="logo" />
                <div v-else class="card-skeleton">
                  <div class="loader"></div>
                </div>
              </div>
            </v-col>
            <v-col>
              <div class="card-audio-text">
                <p>{{ $t("text.gender") }}: {{ gender }}</p>
              </div>
              <div class="card-audio-text">
                <p>{{ $t("text.language") }}: {{ language }}</p>
              </div>
              <div class="card-audio-text">
                <p>{{ $t("text.audio_type") }}: {{ audioType }}</p>
              </div>
              <div class="card-audio-text">
                <p>{{ $t("text.emotions") }}:</p>
              </div>
              <div
                class="card-audio-text-emotions"
                v-for="emotion in emotions"
                :key="emotion.label"
              >
                <p>{{ $t(`feelings.${emotion.label.toLowerCase()}`) }}</p>
                <div class="card-audio-percentage-bar">
                  <div
                    class="card-audio-percentage-bar-bck"
                    :style="{
                      width: `${emotion.percentage.toFixed(2)}%`,
                    }"
                  ></div>
                </div>
              </div>
            </v-col>
          </v-row>
        </v-card-text>
      </v-card>
      <v-alert
        text="La imagen es generada mediante Inteligencia Artificial, por lo que puede tardar unos minutos. Disculpe las molestias."
        title="Alert title"
        type="info"
        variant="tonal"
        class="alert-container"
      ></v-alert>
    </div>
  </div>
</template>

<script>
import LoaderComponent from "@/components/LoaderComponent.vue";
import ErrorAlert from "@/components/ErrorAlert.vue";
import { processAudio, processText } from "@/services/api";
export default {
  components: {
    LoaderComponent,
    ErrorAlert,
  },
  data() {
    return {
      selectedFile: null, // Archivo seleccionado o grabado
      recorder: null, // Instancia de MediaRecorder
      audioChunks: [], // Fragmentos del audio grabado
      isRecording: false, // Estado para alternar entre grabar y detener
      logo: null,
      emotions: [],
      gender: "",
      language: "",
      API_URL: "http://localhost:3000/api",
      imageIsLoaded: false,
      dataIsLoaded: false,
      loading: false,
      progress: 0,
      errorText: "Ha ocurrido un error en la solicitud. Inténtelo de nuevo.",
      showAlert: false,
      isAFile: false,
      progressInterval: null,
      transcription: "",
    };
  },
  methods: {
    // Maneja el cambio en el input de archivo
    handleFileChange(event) {
      const file = event.target.files[0];
      if (file) {
        this.selectedFile = file;
        this.isAFile = true;
      }
    },
    // Alterna entre iniciar y detener la grabación
    async toggleRecording() {
      if (!this.isRecording) {
        await this.startRecording(); // Inicia la grabación
      } else {
        this.stopRecording(); // Detiene la grabación
      }
    },
    closeAlert() {
      this.showAlert = false;
    },
    // Inicia la grabación de audio
    async startRecording() {
      try {
        const stream = await navigator.mediaDevices.getUserMedia({
          audio: true,
        });
        this.recorder = new MediaRecorder(stream);
        this.audioChunks = []; // Reiniciar fragmentos de audio
        this.isRecording = true;

        // Escucha los datos de audio grabados
        this.recorder.ondataavailable = (event) => {
          if (event.data.size > 0) {
            this.audioChunks.push(event.data);
          }
        };

        // Al finalizar la grabación, procesar el audio
        this.recorder.onstop = () => {
          const audioBlob = new Blob(this.audioChunks, { type: "audio/webm" });
          this.selectedFile = new File([audioBlob], "grabacion.webm", {
            type: "audio/webm",
          });

          // Actualiza el input de archivo con el archivo grabado
          const dataTransfer = new DataTransfer();
          dataTransfer.items.add(this.selectedFile);
          this.$refs.fileInput.files = dataTransfer.files;

          console.log("Audio grabado y asignado al input:", this.selectedFile);
          this.isRecording = false;
        };

        // Comenzar a grabar
        this.recorder.start();
        console.log("Grabación iniciada.");
      } catch (error) {
        console.error("Error al iniciar la grabación:", error);
      }
    },
    // Detiene la grabación de audio
    stopRecording() {
      if (this.recorder && this.recorder.state === "recording") {
        this.recorder.stop();
        console.log("Grabación detenida.");
      }
    },

    async sendData() {
      clearInterval(this.progressInterval);
      this.transcription = "";
      this.gender = "";
      this.language = "";
      this.audioType = "";
      this.emotions = [];
      this.loading = true;
      this.progress = 0;
      this.progressInterval = setInterval(() => {
        if (this.progress < 100) {
          this.progress += 1; // Incrementa el progreso cada cierto tiempo
        }
      }, 500);
      if (!this.selectedFile) {
        console.warn("No hay archivo para enviar.");
        return;
      }

      const maxRetries = 3;

      const wait = (ms) => new Promise((resolve) => setTimeout(resolve, ms));

      const attemptRequest = async (action, ...args) => {
        let attempt = 0;
        while (attempt < maxRetries) {
          try {
            const response = await action(...args);

            return response;
          } catch (error) {
            attempt++;
            console.error(
              `Error en el intento ${attempt} para ${action.name}:`,
              error.message
            );

            if (attempt < maxRetries) {
              console.log(
                `Esperando 1 segundo antes del intento ${attempt + 1}`
              );
              await wait(1000);
            } else {
              this.showAlert = true;
              this.loading = false;
              throw new Error(
                `Error persistente en ${action.name}: ${error.message}`
              );
            }
          }
        }
      };

      try {
        // Enviar el archivo para transcripción
        const transcriptionResponse = await attemptRequest(
          processAudio,
          "transcribe-audio",
          this.selectedFile
        );

        // Enviar el archivo para el análisis de género
        const genderResponse = await attemptRequest(
          processAudio,
          "analyze-gender",
          this.selectedFile
        );
        // Enviar el archivo para detectar el idioma
        const languageResponse = await attemptRequest(
          processText,
          "detect-language-ollama",
          transcriptionResponse.transcription
        );

        // Enviar el archivo para detectar el tipo de audio
        const audioTypeResponse = await attemptRequest(
          processAudio,
          "detect-audio-type",
          this.selectedFile
        );
        // Enviar el archivo para analizar emociones
        const emotionsResponse = await attemptRequest(
          processText,
          "analyze-emotions-ollama",
          transcriptionResponse.transcription
        );
        // Ahora que tenemos todos los datos, actualizar los estados
        this.transcription = transcriptionResponse.transcription;
        this.gender = genderResponse.gender;
        this.language = languageResponse.language;
        this.audioType = audioTypeResponse.audioType;
        this.emotions = emotionsResponse.emotions;
        this.dataIsLoaded = true;
        this.loading = false;
        clearInterval(this.progressInterval);
        this.progress = 100;
        const imageResponse = await attemptRequest(
          processText,
          "generate-image-audio",
          this.transcription
        );
        this.logo = `${this.API_URL.replace("/api", "")}${
          imageResponse.imageUrls[0]
        }`;
        this.imageIsLoaded = true;
      } catch (error) {
        console.error("Error final al procesar el audio:", error.message);
      }
    },
  },
  // Envía los datos (archivo o grabación) al servidor o procesa los datos
};
</script>
<style scoped>
.alert-container {
  margin: 10px;
}
</style>
