<template>
  <div v-if="isLoading" class="overlay">
    <div>
      <h1>{{ progress }} %</h1>
    </div>
    <div class="loader-container">
      <div class="loader" :style="{ width: progress + '%' }">
        <div class="loader-decoration"></div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    isLoading: {
      type: Boolean,
      required: true,
      default: false,
    },
    progress: {
      type: Number,
      required: true,
      default: 0, // Valor inicial del progreso
    },
  },
};
</script>
<style scoped>
/* Overlay que cubre la pantalla */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(59, 63, 92, 0.678); /* Fondo translúcido */
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  z-index: 1000; /* Capa superior */
}

/* Contenedor del loader */
.loader-container {
  width: 100%;
  max-width: 600px;
  padding: 20px;
  display: flex;
  justify-content: flex-start; /* Alineación hacia la izquierda */
}

/* Barra de carga animada */
.loader {
  height: 4.8px;
  position: relative;
  background: #fff;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
  box-sizing: border-box;
  transition: width 0.2s ease-in-out; /* Suaviza el progreso */
}

.loader::after,
.loader::before {
  content: "";
  width: 10px;
  height: 1px;
  background: #fff;
  position: absolute;
  top: 9px;
  right: -2px;
  opacity: 0;
  transform: rotate(-45deg) translateX(0px);
  box-sizing: border-box;
  animation: coli1 0.3s linear infinite;
}
.loader::before {
  top: -4px;
  transform: rotate(45deg);
  animation: coli2 0.3s linear infinite;
}

/* Decoración adicional que se mueve con la barra */
.loader-decoration {
  position: absolute;
  right: -5px;
  top: 50%;
  transform: translateY(-50%);
  width: 10px;
  height: 10px;
  background: #fff;
  border-radius: 50%;
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.7);
  animation: pulse 0.5s infinite alternate; /* Efecto de "pulso" */
}

/* Animación de pulso */
@keyframes animFw {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}

@keyframes coli1 {
  0% {
    transform: rotate(-45deg) translateX(0px);
    opacity: 0.7;
  }
  100% {
    transform: rotate(-45deg) translateX(-45px);
    opacity: 0;
  }
}

@keyframes coli2 {
  0% {
    transform: rotate(45deg) translateX(0px);
    opacity: 1;
  }
  100% {
    transform: rotate(45deg) translateX(-45px);
    opacity: 0.7;
  }
}
@import url("https://fonts.googleapis.com/css?family=Pirata+One|Rubik:900");

h1 {
  text-transform: Uppercase;
  margin-bottom: 0.1em;
  font-family: "Rubik", sans-serif;
  font-size: 6rem;
  color: #e4e5e6;
}

h1 {
  position: relative;
  background: linear-gradient(to right, #3b3f5c, #2b2d42);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

h1:before,
h1:after {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
}

h1:before {
  z-index: -1;
  text-shadow: -0.001em -0.001em 1px rgba(255, 255, 255, 0.15);
}

h1:after {
  z-index: -2;
  text-shadow: 10px 10px 10px rgba(0, 0, 0, 0.5),
    20px 20px 20px rgba(0, 0, 0, 0.4), 30px 30px 30px rgba(0, 0, 0, 0.1);
  mix-blend-mode: multiply;
}
</style>
