<template>
  <LoaderComponent :isLoading="loading" :progress="progress" />
  <ErrorAlert
    v-if="showAlert"
    :isVisible="showAlert"
    @close-alert="closeAlert"
    :text="errorText"
  />
  <div class="main-container">
    <div class="sub-container">
      <v-row>
        <v-col cols="8">
          <div class="main-grid">
            <div>
              <h2>{{ $t("home.title") }}</h2>
              <p>
                {{ $t("home.subtitle") }}
              </p>
            </div>
            <div class="main-input">
              <input class="input" v-model="searchText" />
              <v-btn
                @click="searchRedirect"
                prepend-icon="mdi-magnify"
                class="button"
              >
                {{ $t("buttons.search") }}
              </v-btn>
            </div>
          </div>
        </v-col>
        <v-col cols="4">
          <div class="main-logo">
            <img :src="logo" />
          </div>
        </v-col>
      </v-row>
    </div>
  </div>

  <div class="cards-container">
    <v-row class="row-card-container">
      <v-col v-for="card in cards" :key="card.title">
        <SentimentCardComponent
          @click="goToDetails(card)"
          :title="card.title"
          :tag="card.tag"
          :description="card.description"
          :percentage="card.percentage"
          :label="card.sentiment"
        />
      </v-col>
    </v-row>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import LoaderComponent from "@/components/LoaderComponent.vue";
import SentimentCardComponent from "@/components/SentimentCardComponent.vue";
import ErrorAlert from "@/components/ErrorAlert.vue";

export default {
  components: {
    SentimentCardComponent,
    LoaderComponent,
    ErrorAlert,
  },
  data() {
    return {
      showAlert: false,
      loading: false,
      progress: 0,
      logo: require("../assets/FeelFinder_logo.png"),
      errorText: "",
      searchText: "",
    };
  },
  computed: {
    ...mapGetters(["cards"]),
  },
  created() {
    this.fetchCards();
  },
  methods: {
    closeAlert() {
      this.showAlert = false;
    },
    ...mapActions(["fetchCards", "selectCard"]),
    goToDetails(card) {
      this.selectCard(card); // Guardamos la card seleccionada en Vuex
      this.$router.push({
        name: "AnalyzeDetail",
        params: { name: card.title },
      });
    },
    searchRedirect() {
      // Redirigir a AnalyzeView y pasar el término de búsqueda como parámetro
      if (this.searchText.trim()) {
        this.$router.push({
          path: "/analyze",
          query: { search: this.searchText.trim() },
        });
      }
    },
    async fetchCards() {
      this.loading = true;
      let progressInterval = setInterval(() => {
        if (this.progress < 100) {
          this.progress += 1; // Incrementa el progreso cada cierto tiempo
        }
      }, 200);

      try {
        await this.$store.dispatch("fetchCards");
        clearInterval(progressInterval); // Detén el intervalo cuando la carga haya finalizado
        this.progress = 100; // Asegúrate de que el progreso llegue al 100%
      } catch (error) {
        console.error("Error al cargar las tarjetas:", error);
        this.errorText =
          "Ha ocurrido un error durante la carga de los comentarios. Por favor recargue la página.";
        this.showAlert = true;
        clearInterval(progressInterval); // Detén el intervalo en caso de error
      } finally {
        this.loading = false;
      }
    },
  },
};
</script>

