<template>
  <div class="main-container2">
    <section class="hero">
      <div class="hero-content">
        <h1 class="hero-title">{{ $t("home.contact") }}</h1>
        <p class="hero-subtitle">
          {{ $t("text.contact_subtitle") }}
        </p>
      </div>
    </section>
    <section class="contact-form">
      <div class="form-container">
        <h2>{{ $t("text.contact_form_title") }}</h2>
        <form @submit.prevent="sendMessage">
          <div class="form-group">
            <label for="name">{{ $t("text.contact_form_label1") }}</label>
            <input
              v-model="formData.name"
              type="text"
              id="name"
              name="name"
              :placeholder="$t('text.contact_form_text1')"
              required
            />
          </div>
          <div class="form-group">
            <label for="email">{{ $t("text.contact_form_label2") }}</label>
            <input
              v-model="formData.email"
              type="email"
              id="email"
              name="email"
              :placeholder="$t('text.contact_form_text2')"
              required
            />
          </div>
          <div class="form-group">
            <label for="message">{{ $t("text.contact_form_label3") }}</label>
            <textarea
              v-model="formData.message"
              id="message"
              name="message"
              rows="4"
              :placeholder="$t('text.contact_form_text3')"
              required
            ></textarea>
          </div>
          <button type="submit" class="submit-button">
            {{ $t("buttons.send") }}
          </button>
        </form>
      </div>
    </section>
  </div>
  <EmailLoader :loading="isLoading" :loaded="isLoaded" />
</template>

<script>
import { sendContactMessage } from "@/services/api";
import EmailLoader from "@/components/EmailLoader.vue"; // Importa el componente

export default {
  name: "Contact",
  data() {
    return {
      formData: {
        name: "",
        email: "",
        message: "",
      },
      isLoading: false,
      isLoaded: false,
    };
  },
  methods: {
    async sendMessage() {
      this.isLoading = true;
      try {
        const { name, email, message } = this.formData;
        const response = await sendContactMessage(name, email, message);
        this.isLoaded = true;
        setTimeout(() => {
          this.isLoading = false;
          this.isLoaded = false;
          this.formData.name = "";
          this.formData.email = "";
          this.formData.message = "";
        }, 2000);
      } catch (error) {
        alert("Error al enviar el mensaje: " + error.message);
      }
    },
  },
  components: {
    EmailLoader, // Registra el componente
  },
};
</script>

<style scoped>
.hero {
  background: linear-gradient(135deg, var(--main-color), var(--bad-color));
  color: var(--card-secondary);
  text-align: center;
  padding: 60px 20px;
  border-radius: 8px;
  margin: 20px 10px 0px 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.hero-content {
  max-width: 800px;
  margin: 0 auto;
}

.hero-title {
  font-size: 3em;
  font-weight: bold;
  margin-bottom: 10px;
}

.hero-subtitle {
  font-size: 1.2em;
  color: white;
}

.contact-form {
  display: flex;
  justify-content: center;
}

.form-container {
  background-color: var(--card-secondary);
  padding: 30px;
  border-radius: 12px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 600px;
}

h2 {
  color: var(--main-color);
  margin-bottom: 20px;
  font-size: 1.8em;
}

.form-group {
  margin-bottom: 20px;
}

label {
  display: block;
  margin-bottom: 8px;
  font-weight: bold;
  color: var(--text-color);
}

input,
textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid var(--secondary-color);
  border-radius: 6px;
  font-size: 1em;
  color: var(--text-color);
  box-sizing: border-box;
}

input:focus,
textarea:focus {
  border-color: var(--main-color);
  outline: none;
  box-shadow: 0 0 4px var(--main-color);
}

.submit-button {
  height: 3rem !important;
  color: var(--card-secondary);
  background-color: var(--highlight-color);
  transition: 0.5s;
  align-items: center;
  border-radius: 4px;
  display: inline-grid;
  font-weight: 500;
  justify-content: center;
  letter-spacing: 0.0892857143em;
  line-height: normal;
  max-width: 100%;
  outline: none;
  position: relative;
  text-decoration: none;
  text-indent: 0.0892857143em;
  text-transform: uppercase;
  padding: 0 16px;
  box-shadow: 0px 2px 4px -1px var(--v-shadow-key-umbra-opacity, rgba(0, 0, 0, 0.2)),
    0px 4px 5px 0px var(--v-shadow-key-penumbra-opacity, rgba(0, 0, 0, 0.14)),
    0px 1px 10px 0px var(--v-shadow-key-ambient-opacity, rgba(0, 0, 0, 0.12));
}

.submit-button:hover {
  background-color: var(--bad-color);
}

.credits {
  text-align: center;
  margin-top: 40px;
  font-size: 0.9em;
  color: var(--secondary-color);
}
</style>
