<template>
  <div class="main-container2">
    <div class="sub-detail-container">
      <v-row>
        <v-col>
          <v-card class="card-detail">
            <div
              :class="{
                'card-detail-background-good': card.sentiment == 'POSITIVE',
                'card-detail-background-neutral': card.sentiment == 'NEUTRAL',
                'card-detail-background-bad': card.sentiment == 'NEGATIVE',
              }"
            ></div>
            <v-card-title class="card-detail-title">
              <p class="date-title">{{ formatDate(card.date) }}</p>
              <h2 class="detail-title">{{ truncateTitle(card.title) }}</h2>
              <h3 class="detail-subtitle">
                {{ card.tag }} by {{ card.author_thread }}
              </h3>
            </v-card-title>
            <v-card-text class="card-detail-content">
              <div>
                <p class="detail-desc">
                  {{ truncateDescription(card.description) }}
                </p>
              </div>
              <div class="info-detail-container">
                <p>{{ $t("text.author") }}: {{ card.author }}</p>
                <v-btn @click="goToURL(card.link)" class="button">
                  <v-icon class="button-icon">mdi mdi-search-web</v-icon>
                </v-btn>
              </div>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <h3 class="detail-subtitle3">
            {{ $t("text.percentage") }}
            {{ $t(`buttons.${card.sentiment.toLowerCase()}`) }}
          </h3>
          <div class="percentage-bar">
            <div
              :class="{
                'percentage-good': card.sentiment == 'POSITIVE',
                'percentage-neutral': card.sentiment == 'NEUTRAL',
                'percentage-bad': card.sentiment == 'NEGATIVE',
              }"
              :style="{ width: `${parseFloat(percentageValue).toFixed(2)}%` }"
            ></div>
            <p class="percentage-text">
              {{ parseFloat(percentageValue).toFixed(2) }}%
            </p>
          </div>
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <div class="button-tag-container">
            <div v-for="button in card.emotions">
              <v-btn :key="button" class="button-tag">{{
                $t(`feelings.${button.label.toLowerCase()}`)
              }}</v-btn>
            </div>
          </div>
        </v-col>
      </v-row>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      percentageValue: 0,
      card: null,
    };
  },
  computed: {
    ...mapGetters(["selectedCard"]),
  },
  created() {
    const selectedCard = this.$store.state.selectedCard; // Recuperar del store
    if (selectedCard) {
      this.card = selectedCard; // Asignar al estado local para usar en la vista
      this.percentageValue = this.card.percentage;
    } else {
      console.warn("No se encontró la card seleccionada, redirigiendo...");
      this.$router.push({ name: "home" }); // Redirigir si no hay card seleccionada
    }
  },
  methods: {
    formatDate(timestamp) {
      const date = new Date(timestamp * 1000);
      const year = date.getFullYear();
      const month = String(date.getMonth() + 1).padStart(2, "0");
      const day = String(date.getDate()).padStart(2, "0");

      return `${day}/${month}/${year}`;
    },
    goToURL(link) {
      window.open(link, "_blank");
    },
    truncateTitle(title) {
      const limit = 120; // Límite de caracteres para el título
      return title.length > limit ? `${title.slice(0, limit)}...` : title;
    },
    truncateDescription(description) {
      const limit = 400; // Límite de caracteres para la descripción
      return description.length > limit
        ? `${description.slice(0, limit)}...`
        : description;
    },
  },
};
</script>

<style scoped></style>
