// api.js
import axios from "axios";

// Base URL de la API
const API_BASE_URL = "http://169.254.123.252:3000/api";

// Rutas existentes
const API_URL = `${API_BASE_URL}/comments`;
const API_URL_ALL = `${API_BASE_URL}/multiple-comments`;

// Función para obtener comentarios basados en subreddit
export const getComments = async (subreddit) => {
  try {
    const response = await axios.get(API_URL, {
      params: { query: subreddit },
    });
    return response.data;
  } catch (error) {
    throw new Error("Error al obtener comentarios: " + error.message);
  }
};

// Función para obtener los comentarios principales
export const getTopComments = async () => {
  try {
    const response = await axios.get(API_URL_ALL);
    return response.data;
  } catch (error) {
    throw new Error("Error al obtener comentarios: " + error.message);
  }
};

// Función para procesar un archivo de audio
export const processAudio = async (type, file) => {
  try {
    const formData = new FormData();
    formData.append("file", file);

    const response = await axios.post(`${API_BASE_URL}/${type}`, formData, {
      headers: { "Content-Type": "multipart/form-data" },
    });

    return response.data; // Retorna solo los datos relevantes de la respuesta
  } catch (error) {
    throw new Error(`Error al procesar el audio (${type}): ` + error.message);
  }
};

// Función para procesar texto
export const processText = async (type, text) => {
  try {
    const response = await axios.post(
      `${API_BASE_URL}/${type}`,
      { text },
      {
        headers: { "Content-Type": "application/json" },
      }
    );
    return response.data;
  } catch (error) {
    throw new Error(`Error al procesar el texto (${type}): ` + error.message);
  }
};

// Función para enviar un mensaje de contacto
export const sendContactMessage = async (name, email, message) => {
  try {
    const response = await axios.post(`${API_BASE_URL}/send-email`, {
      name,
      email,
      message,
    });
    return response.data;
  } catch (error) {
    throw new Error("Error al enviar el mensaje de contacto: " + error.message);
  }
};
